.form-create-sfa {
    .feedback {
        display: none;
    }

    [class|="col"] {
        padding: 12.5px;
    }

    .valid-feedback {
        > div {
            display: inline;

            > button.btn.btn-link {
                display: none;
            }
        }
    }

    .tracking-label, .agency-label {

        height: 24px;
        $iconSize: 20px;

        .btn {
            display: inline-flex;
            align-items: center;
            justify-content: center;
            margin-left: 5px;

            > svg {
                width: $iconSize;
                height: $iconSize;
            }
        }
    }

    .react-datepicker-wrapper {
        width: 100%;
    }

    .tracking-input {
        position: relative;

        > input {
            max-width: 70%;
        }

        > .btn {
            position: absolute;
            bottom: 0;
            right: 0;
            max-width: 25%;
            height: 60px;
            padding: 0;
        }
    }
}